:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

body {
  margin-bottom: 20px;
}

.search-form {
  z-index: 10;
  width: 100%;
  background-color: coral;
  justify-content: center;
  gap: 20px;
  padding: 20px 0;
  display: flex;
  position: fixed;
}

.input {
  border-radius: 15px;
}

.load-more {
  background-color: #fff;
  border-radius: 15px;
  margin-top: 10px;
  margin-left: 45%;
  padding: 5px 10px;
}

.submit {
  background-color: #fff;
  border-radius: 15px;
  padding: 5px 10px;
}

.load-more:hover, .submit:hover {
  background-color: wheat;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  padding-top: 90px;
  display: flex;
}

.photo-card {
  width: 320px;
  border-radius: 2%;
  overflow: hidden;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.info {
  justify-content: space-around;
  display: flex;
}

.info-item {
  color: #000;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.photo-card:hover {
  transform: scale(1.03);
}

.thumb {
  width: 320px;
  height: 77%;
  overflow: hidden;
}

.photo-image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.photo-link {
  height: 100%;
  text-decoration: none;
  display: block;
}

.photo-link:hover {
  cursor: zoom-in;
}

